export default {
    name: "my_travelnotes",
    data() {
        return {
            travelnotesList: [],
        }
    },
    onShow: function () {
        this.getMyTravelNotes()
        console.log(this.travelnotesList)
    },
    methods: {
        deleteTravelNotes(item) {
            var _this = this
            uni.showModal({
                title: '删除',
                content: '确认删除这条回忆吗？',
                success: function (res) {
                    if (res.confirm) {
                        uni.request({
                            url: _this.$BASE_HOST + 'travelnotes/delete',
                            method: 'GET',
                            data: {
                                id: item.id
                            },
                            success: (res) => {
                                console.log(res)
                                _this.getMyTravelNotes()
                            }
                        })
                    } else if (res.cancel) {
                        console.log('用户点击取消');
                    }
                }
            });
        },
        getMyTravelNotes() {
            var _this = this
            uni.request({
                url: _this.$BASE_HOST + 'travelnotes/getAllMyNotes',
                method: 'GET',
                data: {
                    user_id: uni.getStorageSync('local_userinfo').id
                },
                success: (res) => {
                    _this.travelnotesList = []
                    if (_this.travelnotesList != null) {
                        res.data.data.forEach((item, index) => {
                            item.note_img = item.note_img.split(',')
                            for(var x=0;x<item.note_img.length;x++){
                                item.note_img[x] = _this.$BASE_HOST + item.note_img[x]
                            }
                            item.note_video = _this.$BASE_HOST + item.note_video
                            item.user_avatar = 'background-image:url(' + item.user_avatar +')'
                            item.create_date = item.create_date.substring(0,10)

                            _this.travelnotesList.push(item)
                        })
                    } else {
                        _this.travelnotesList = null
                    }
                }
            })
        }
    }
}
